.dashboard-container {
    background-image: url('https://res.cloudinary.com/dtomajdlh/image/upload/v1722419770/dashboard-bg.jpg');
    background-size: cover;
    min-height: 100vh;
    padding: 20px;
    padding-left: 50px;
  }
  .card-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .dashboard-heading {
    font-family: 'Roboto';
    font-size: 40px;
    font-weight: bold;
    color: #66f0fa;
  }
  .link {
    text-decoration: none;
    color: transparent;
  }
  .custom-button {
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
    border: none;
    outline: none;
    font-family: 'Roboto';
    font-size: 15px;
    background-color: #0c43eb;
    color: white;
    border-radius: 5px;
  }
  .profile-image {
    height: 200px;
    width: 200px;
    border-radius: 20px;
  }
  .main-text {
    font-family: 'Roboto';
    font-size: 25px;
    color: #66f0fa;
    font-weight: 500;
  }
  .text {
    font-size: 20px;
    color: white;
    font-weight: 400;
  }
  
  @media (max-width: 765px) {
    .dashboard-container {
      padding-left: 20px;
    }
    .dashboard-heading {
      font-size: 28px;
    }
    .main-text {
      font-size: 20px;
    }
    .text {
      font-size: 17px;
    }
    .custom-button {
      width: 53px;
      padding-left: 5px;
      padding-right: 5px;
      font-size: 13px;
    }
  }
  