.register-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #b8f5f2;
    background-size: cover;
    padding: 20px;
  }
  .banner-image {
    width: 55%;
    height: 700px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  .form-container {
    width: 45%;
    height: 700px;
    padding: 20px;
    background-color: white;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .heading {
    font-family: 'Roboto';
    font-size: 35px;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .already-text {
    font-family: 'Roboto';
    font-size: 17px;
    margin-top: 0px;
    margin-bottom: 30px;
  }
  .sign-link {
    text-decoration: none;
    color: #0c43eb;
    margin-left: 5px;
  }
  .input-container {
    width: 80%;
    margin-bottom: 20px;
  }
  .label-text {
    font-family: 'Roboto';
    font-size: 17px;
    color: #464f4c;
  }
  .text-input {
    width: 100%;
    height: 45px;
    background-color: transparent;
    border-radius: 3px;
    font-family: 'Roboto';
    font-size: 17px;
    padding-left: 10px;
    border: 1px solid #7c8784;
    color: #464f4c;
    margin-top: 5px;
  }
  .required-msg {
    font-family: 'Roboto';
    font-size: 15px;
    color: #f7072b;
    margin-top: 5px;
    margin-bottom: 0px;
  }
  .submit-button {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: #0c43eb;
    width: 80%;
    height: 45px;
    font-family: 'Roboto';
    font-size: 20px;
    font-weight: 500;
    color: white;
    border-radius: 25px;
    margin-top: 20px;
  }
  
  @media (max-width: 765px) {
    .register-container {
      flex-direction: column;
    }
    .banner-image {
      width: 95%;
      height: 300px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      border-bottom-left-radius: 0px;
    }
    .form-container {
      width: 95%;
      height: 650px;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;
      border-top-right-radius: 0px;
      justify-content: flex-start;
    }
    .heading {
      font-size: 30px;
    }
    .already-text {
      font-size: 15px;
    }
    .input-container {
      margin-bottom: 15px;
    }
    .label-text {
      font-size: 16px;
    }
    .text-input {
      font-size: 16px;
      height: 40px;
    }
    .submit-button {
      font-size: 16px;
    }
  }
  