.login-page-container {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #b8f5f2;
    padding: 10px;
  }
  .login-container {
    background-color: #ffffff;
    width: 50%;
    height: 580px;
    border-radius: 8px;
    box-shadow: 0px 4px 16px 0px #7b879429;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  .login-image {
    width: 300px;
    height: 150px;
    margin-bottom: 30px;
    border-radius: 10px;
  }
  .details-container {
    width: 80%;
    margin-top: 20px;
  }
  .username-label {
    font-family: Roboto;
    font-size: 12px;
    font-weight: 700;
    color: #7e858e;
  }
  .username-input {
    width: 100%;
    height: 40px;
    border-radius: 2px;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    border: 1px solid #d7dfe9;
    background-color: transparent;
    color: #263868;
    padding-left: 10px;
    margin-top: 5px;
  }
  .show-container {
    width: 80%;
    display: flex;
    align-items: center;
    margin-top: 5px;
  }
  .checkbox-input {
    width: 16px;
    height: 16px;
    margin-right: 8px;
    margin-top: 8px;
    cursor: pointer;
  }
  .show-label {
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 400;
    color: #000000;
    cursor: pointer;
  }
  .login-button {
    width: 80%;
    height: 40px;
    border-radius: 8px;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 700;
    color: #242624;
    background-color: #b8f5f2;
    cursor: pointer;
    margin-top: 30px;
    border: none;
  }
  .error-msg {
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    color: #ef4444;
    margin-left: 30px;
    margin-top: 5px;
    width: 80%;
  }
  .required-msg {
    font-family: 'Roboto';
    font-size: 15px;
    color: #f7072b;
    margin-top: 5px;
    margin-bottom: 0px;
  }
  
  @media (max-width: 765px) {
    .login-container {
      width: 100%;
      box-shadow: none;
    }
    .details-container {
      width: 100%;
    }
    .username-input {
      width: 100%;
    }
    .show-container {
      width: 100%;
    }
    .login-button {
      width: 100%;
    }
    .error-msg {
      margin-left: 0px;
    }
  }
  